import { useState } from 'react';
import '../components/cardstyle.css';
import Items from '../components/templatecard';
import Cart from '../components/cart';

function Addcard(){
  let itemDetails = [{
    id:1,
    itemNo:'Item 1',
    price:15,
    description:'This is item one',
    rating:4
  },
  {
    id:2,
    itemNo:'Item 2',
    price:10,
    description:'This is item two',
    rating:3

  },{
    id:3,
    itemNo:'Item 3',
    price:20,
    description:'This is item two',
    rating:3

  },
  {
    id:4,
    itemNo:'Item 4',
    price:30,
    description:'This is item two',
    rating:3

  }]

 const [cartitem,setCartItem] = useState([]);
 const [total,setTotal] = useState(0); 
 
 let onAdd = (product) => {

  setCartItem([...cartitem, product])
  setTotal(total + product.price)
}

let onRemove = (item) => {
  setCartItem(cartitem.filter(obj=>obj.id !== item.id))
  setTotal(total - item.price)
}


return (
    <div className="container-fluid">
 <nav class="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
    <div class="container">
      <a class="navbar-brand" href="#">Start Bootstrap</a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarResponsive">
        <ul class="navbar-nav ml-auto">
          <li class="nav-item active">
            <a class="nav-link" href="#">Home
              <span class="sr-only">(current)</span>
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#">About</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#">Services</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" href="#">Contact</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>

  <div class="container">

    <div class="row">

      <div class="col-lg-3">

        <h1 class="my-4">CART</h1>
        <div class="card" style={{width: "18rem;"}}>
    <ul class="list-group list-group-flush" >
          
           <Cart cartitem={cartitem} onAdd={onAdd} onRemove={onRemove}>
           
           </Cart>

           </ul>
           </div>
            <h3>{cartitem.length == 0 ? 'Cart is empty' : `Total : ${total}`}</h3>

      </div>


      <div class="col-lg-9">

        <div id="carouselExampleIndicators" class="carousel slide my-4" data-ride="carousel">
          <ol class="carousel-indicators">
            <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
            <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
          </ol>
          <div class="carousel-inner" role="listbox">
            <div class="carousel-item active">
              <img class="d-block img-fluid" src="http://placehold.jp/900x350.png" alt="First slide"/>
            </div>
            <div class="carousel-item">
              <img class="d-block img-fluid" src="http://placehold.jp/900x350.png" alt="Second slide"/>
            </div>
            <div class="carousel-item">
              <img class="d-block img-fluid" src="http://placehold.jp/900x350.png" alt="Third slide"/>
            </div>
          </div>
          <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
            <span class="sr-only">Previous</span>
          </a>
          <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
            <span class="carousel-control-next-icon" aria-hidden="true"></span>
            <span class="sr-only">Next</span>
          </a>
        </div>

        <div class="row">
      
      {
        itemDetails.map((obj)=>{
          return(<Items data={obj} onAdd={onAdd}></Items>);
        })
      }
      
        </div>
 
      </div>
  

    </div>
    

  </div>

  <footer class="py-5 bg-dark">
    <div class="container">
      <p class="m-0 text-center text-white">Copyright &copy; Your Website 2020</p>
    </div>

  </footer>
    </div>
  );
}

export default Addcard;




 