function Cart(props){

const { cartitem,onRemove,setButtons } = props;

    

    return(
<>
          
   {
   cartitem.map((item)=>{
       return (
           <li className="list-group=item" style={{listStyle: "none;"}}>{item.itemNo} - ${item.price}
                <button className="btn btn-sm btn-danger ml-1"  onClick={()=>{
                        onRemove(item);
                        
                }}
                    > X </button>
           </li>
       );
    })
    
   }
         
</>
    );
}

export default Cart;