import { useState } from "react";


function Items(props){
  const [buttons,setButtons] = useState(false);
  const { onAdd,cartitem } = props;



return (   
   <div class="col-lg-4 col-md-6 mb-4">
<div class="card h-100">
  <a href="#"><img class="card-img-top" src="http://placehold.jp/700x400.png" alt=""/></a>
  <div class="card-body">
    <h4 class="card-title">
      <a href="#">{props.data.itemNo}</a>
    </h4>
    <h5>{props.data.price}</h5>
    <p class="card-text">{props.data.description}</p>
    
    <button className="btn btn-primary"  disabled={buttons}  onClick={()=>{
       props.onAdd(props.data) ;
        setButtons(true);
      
    }}>ADD</button>
  </div>
{/*   <div class="card-footer">  this.disabled=true : props.onAdd(props.data)
    <small class="text-muted">{props.data.rating}</small>
  </div> */}
</div>
</div>
);
}

export default Items;